import { createSlice, createAsyncThunk, PayloadAction } from '@reduxjs/toolkit';
import api from '@/utils/axios';
import { setToken, getToken, clearAuthData } from '@/utils/auth';
import { getApiUrl } from '@/utils/apiConfig';

interface User {
  _id: string;
  name: string;
  email: string;
  profileImage?: string;
}

interface AuthState {
  user: User | null;
  token: string | null;
  isAuthenticated: boolean;
  loading: boolean;
  error: string | null;
}

interface LoginResponse {
  token: string;
  user: User;
}

interface RegisterResponse {
  token: string;
  user: User;
}

const initialState: AuthState = {
  user: null,
  token: null,
  isAuthenticated: false,
  loading: false,
  error: null,
};

export const login = createAsyncThunk<LoginResponse, { email: string; password: string }>(
  'auth/login',
  async (credentials, { rejectWithValue, dispatch }) => {
    try {
      console.log(' login   :', credentials);
      console.log('  URL:', `${getApiUrl()}/auth/login`);
      
      const response = await api.post<LoginResponse>('/auth/login', credentials);
      console.log('  :', response.data);
      
      //  
      if (response.data.token) {
        try {
          // 1.    
          console.log('  :', response.data.token.substring(0, 20) + '...');
          
          // 2. auth.ts setToken  
          setToken(response.data.token);
          console.log('   ');
          
          // 3.    
          const savedToken = getToken();
          console.log('     :', savedToken ? savedToken.substring(0, 20) + '...' : 'null');
          
          if (!savedToken) {
            console.error('    -    null');
            return rejectWithValue('  .');
          }
          
          // 4.   
          localStorage.setItem('user', JSON.stringify(response.data.user));
          console.log('    ');
          
          // 5. Redux  
          console.log(' Redux   ');
          dispatch(setCredentials({ 
            token: response.data.token, 
            user: response.data.user 
          }));
          
          // 6. localStorage   
          console.log(' localStorage  :', { ...localStorage });
          
          // 7.  
          const finalToken = getToken();
          console.log('   :', finalToken ? finalToken.substring(0, 20) + '...' : 'null');
          
          if (!finalToken) {
            throw new Error('  .');
          }
        } catch (error) {
          console.error(' /   :', error);
          clearAuthData(); //       
          return rejectWithValue('   .');
        }
      } else {
        console.error('   :', response.data);
        return rejectWithValue('  .');
      }
      
      return response.data;
    } catch (error: any) {
      console.error('  :', error);
      return rejectWithValue(error.response?.data?.message || ' .');
    }
  }
);

export const register = createAsyncThunk<RegisterResponse, { name: string; email: string; password: string }>(
  'auth/register',
  async (userData, { rejectWithValue }) => {
    try {
      console.log(' register   :', userData);
      console.log('  URL:', '/auth/register');
      const response = await api.post<RegisterResponse>('/auth/register', userData);
      console.log('  :', response.data);
      return response.data;
    } catch (error: any) {
      console.error('  :', error);
      return rejectWithValue(error.response?.data?.message || ' .');
    }
  }
);

export const logout = createAsyncThunk('auth/logout', async () => {
  console.log('   ');
  clearAuthData();
  console.log('    ');
});

export const getCurrentUser = createAsyncThunk<User>(
  'auth/getCurrentUser',
  async (_, { rejectWithValue }) => {
    try {
      console.log('    ');
      console.log('  URL:', `${getApiUrl()}/auth/me`);
      const response = await api.get<User>('/auth/me');
      console.log('   :', response.data);
      return response.data;
    } catch (error: any) {
      console.error('    :', error);
      return rejectWithValue(error.response?.data?.message || '   .');
    }
  }
);

const authSlice = createSlice({
  name: 'auth',
  initialState,
  reducers: {
    setCredentials: (state, action: PayloadAction<{ user: User; token: string }>) => {
      const { user, token } = action.payload;
      state.user = user;
      state.token = token;
      state.isAuthenticated = true;
    },
    clearCredentials: (state) => {
      state.user = null;
      state.token = null;
      state.isAuthenticated = false;
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(login.pending, (state) => {
        state.loading = true;
        state.error = null;
      })
      .addCase(login.fulfilled, (state, action) => {
        state.loading = false;
        state.user = action.payload.user;
        state.token = action.payload.token;
        state.isAuthenticated = true;
        state.error = null;
      })
      .addCase(login.rejected, (state, action) => {
        state.loading = false;
        state.error = action.payload as string;
      })
      .addCase(register.pending, (state) => {
        state.loading = true;
        state.error = null;
      })
      .addCase(register.fulfilled, (state, action) => {
        state.loading = false;
        state.user = action.payload.user;
        state.token = action.payload.token;
        state.isAuthenticated = true;
        state.error = null;
      })
      .addCase(register.rejected, (state, action) => {
        state.loading = false;
        state.error = action.payload as string;
      })
      .addCase(logout.fulfilled, (state) => {
        state.user = null;
        state.token = null;
        state.isAuthenticated = false;
        state.error = null;
      })
      .addCase(getCurrentUser.pending, (state) => {
        state.loading = true;
        state.error = null;
      })
      .addCase(getCurrentUser.fulfilled, (state, action) => {
        state.loading = false;
        state.user = action.payload;
        state.isAuthenticated = true;
        state.error = null;
      })
      .addCase(getCurrentUser.rejected, (state, action) => {
        state.loading = false;
        state.error = action.payload as string;
      });
  },
});

export const { setCredentials, clearCredentials } = authSlice.actions;
export default authSlice.reducer;