import axios from 'axios';

interface ApiConfig {
  apiUrl: string;
  wsUrl: string;
  version?: string;
  env?: string;
}

//  API 
let apiConfig: ApiConfig = {
  apiUrl: import.meta.env.VITE_API_URL || 'https://lovetree250411.onrender.com/api',
  wsUrl: import.meta.env.VITE_WS_URL || (window.location.protocol === 'https:'
    ? 'wss://' + window.location.host
    : 'ws://' + window.location.host),
};

//    
let isConfigInitialized = false;

// API    
const saveApiConfig = (config: ApiConfig) => {
  localStorage.setItem('api_config', JSON.stringify(config));
  console.log(' API  :', config);
};

// API    
const loadApiConfig = (): ApiConfig | null => {
  const saved = localStorage.getItem('api_config');
  if (saved) {
    try {
      const parsedConfig = JSON.parse(saved);
      console.log('   API  :', parsedConfig);
      return parsedConfig;
    } catch (e) {
      console.error('  API   :', e);
      return null;
    }
  }
  return null;
};

// API   (   )
export const initApiConfig = async (): Promise<ApiConfig> => {
  //     
  if (isConfigInitialized) {
    return apiConfig;
  }

  //     ( )
  apiConfig = {
    apiUrl: import.meta.env.VITE_API_URL || 'https://lovetree250411.onrender.com/api',
    wsUrl: import.meta.env.VITE_WS_URL || (window.location.protocol === 'https:'
      ? 'wss://' + window.location.host
      : 'ws://' + window.location.host),
  };
  isConfigInitialized = true;
  return apiConfig;
};

//  API URL  ( )
export const getApiUrl = (): string => {
  if (!isConfigInitialized) {
    console.warn(' API   .  .');
  }
  return apiConfig.apiUrl;
};

//  WebSocket URL  ( )
export const getWsUrl = (): string => {
  if (!isConfigInitialized) {
    console.warn(' API   .  .');
  }
  return apiConfig.wsUrl;
};

//     ()
export const updateApiConfig = (newConfig: Partial<ApiConfig>) => {
  apiConfig = { ...apiConfig, ...newConfig };
  saveApiConfig(apiConfig);
  isConfigInitialized = true;
  console.log(' API   :', apiConfig);
  return apiConfig;
};

export default {
  initApiConfig,
  getApiUrl,
  getWsUrl,
  updateApiConfig
}; 