import axios, {
  InternalAxiosRequestConfig,
  AxiosResponse,
  AxiosError,
  isAxiosError, // isAxiosError  
} from 'axios';
import { getToken, clearAuthData } from './auth';
import { getApiUrl } from './apiConfig';

// vite-env.d.ts   
const isDevelopment = import.meta.env.MODE === 'development';

let logoutHandler: (() => void) | null = null;
export const setLogoutHandler = (handler: () => void) => {
  logoutHandler = handler;
  if (isDevelopment) {
    console.log('   .');
  }
};

const publicApiPaths = ['/auth/login', '/auth/register', '/auth/health'];

//      (  )
interface ErrorResponse {
  message: string;
  status?: number;
  code?: string;
}

// YouTube API   axios  
export const youtubeApi = axios.create({
  baseURL: 'https://www.googleapis.com/youtube/v3',
  timeout: 10000,
});

//     axios 
export const configApi = axios.create({
  timeout: 5000,
});

// --- : updateApiInstance       export   . ---
//       .
// export const updateApiInstance = () => { ... }

//  baseURL  API   
export const createApi = () => {
  const baseURL = getApiUrl();
  if (isDevelopment) {
    console.log(' [axios.ts] API baseURL:', baseURL);
  }

  const apiInstance = axios.create({
    baseURL,
    timeout: 10000,
    headers: {
      'Content-Type': 'application/json',
    },
    withCredentials: true,
  });

  //  
  apiInstance.interceptors.request.use(
    (config: InternalAxiosRequestConfig) => {
      const token = getToken();
      const url = config.url || '';
      const isPublicApi = publicApiPaths.some(path => url.includes(path));

      if (token && !isPublicApi) {
        config.headers.Authorization = `Bearer ${token}`;
        if (isDevelopment) {
          console.log(' [axios.ts]   :', token.substring(0, 20) + '...');
        }
      } else if (!isPublicApi && isDevelopment) {
        if (!['/login', '/register'].some(path => url.includes(path))) {
          console.warn(' [axios.ts]     :', config.url);
        }
      }

      if (isDevelopment) {
        console.log(' [axios.ts] :', {
          url: config.url,
          method: config.method,
          data: config.data,
          isPublicApi,
        });
      }
      return config;
    },
    (error: AxiosError) => {
      console.error(' [axios.ts]   :', error);
      return Promise.reject(error);
    }
  );

  //  
  apiInstance.interceptors.response.use(
    (response: AxiosResponse) => {
      if (isDevelopment) {
        console.log(' :', {
          status: response.status,
          data: response.data,
        });
      }
      return response;
    },
    async (error: any) => {
      if (isAxiosError(error)) {
        if (isDevelopment) {
          console.error(' [axios.ts] Axios  :', {
            status: error?.response?.status,
            data: error?.response?.data,
            message: typeof error?.message === 'string' ? error.message : '',
            code: typeof error?.code === 'string' ? error.code : '',
          });
        }
        if (axios.isCancel(error)) {
          console.log(' [axios.ts]  .');
          return Promise.reject(new Error('Request canceled'));
        }
        // @ts-ignore
        if (error?.response?.status === 401) {
          console.warn(' [axios.ts] 401 Unauthorized -   ');
          clearAuthData();
          if (logoutHandler) {
            logoutHandler();
          } else {
            if (window.location.pathname !== '/login') {
              window.location.href = '/login';
            }
          }
          return Promise.reject(new Error('   '));
        }
        // @ts-ignore
        if (typeof error?.code === 'string' && error.code === 'ECONNABORTED') {
          console.error(' [axios.ts]   ');
          return Promise.reject(new Error('  .'));
        }
        // @ts-ignore
        if (!error?.response) {
          // @ts-ignore
          console.error(' [axios.ts]     :', typeof error?.message === 'string' ? error.message : '');
          return Promise.reject(new Error('   .'));
        }
        //      
        // @ts-ignore
        const errorData = error?.response?.data as ErrorResponse | undefined;
        // @ts-ignore
        const errorMessage = (errorData?.message && typeof errorData.message === 'string')
          ? errorData.message
          : (// @ts-ignore
            typeof error?.message === 'string' ? error.message : '   API  .');
        return Promise.reject(new Error(errorMessage));
      } else {
        // AxiosError    
        const err: any = error;
        console.error(' [axios.ts]   :', err);
        const errorMessage = typeof err?.message === 'string' ? err.message : '    .';
        return Promise.reject(new Error(errorMessage));
      }
    }
  );

  return apiInstance;
};

//  API  
const api = createApi();

// --- :  export  ---
//  Netlify     export    .
// export { api, updateApiInstance }; // <<<    !

// ---   default export ---
export default api;