import React, { useEffect, useState } from 'react';
import ReactDOM from 'react-dom/client';
import { BrowserRouter } from 'react-router-dom';
import { Provider } from 'react-redux';
import { QueryClient, QueryClientProvider } from '@tanstack/react-query';
import App from './App';
import { store } from './store';
import './index.css'; // Tailwind styles
import '../styles/global.css'; // Global reset and base styles
import { initApiConfig, getApiUrl, getWsUrl, updateApiConfig } from './utils/apiConfig';

const queryClient = new QueryClient();

// API    
const ApiConfigInitializer: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  const [isInitialized, setIsInitialized] = useState(false);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const initialize = async () => {
      try {
        console.log(' API   ...');
        const config = await initApiConfig();
        console.log(' API   :', config);
        
        //   
        const apiUrl = getApiUrl();
        const wsUrl = getWsUrl();
        
        console.log('  API URL:', apiUrl);
        console.log('  WebSocket URL:', wsUrl);
        
        setIsInitialized(true);
      } catch (err) {
        console.error(' API    :', err);
        setError(`API   : ${err}`);
        //    
        setIsInitialized(true);
      }
    };

    initialize();
  }, []);

  //     
  if (!isInitialized) {
    return (
      <div className="flex flex-col items-center justify-center h-screen bg-gray-100">
        <div className="text-xl font-bold mb-4">API   ...</div>
        <div className="w-12 h-12 border-4 border-blue-500 border-t-transparent rounded-full animate-spin"></div>
      </div>
    );
  }

  //      
  if (error) {
    console.warn('    :', error);
  }

  return <>{children}</>;
};

ReactDOM.createRoot(document.getElementById('root')!).render(
  <React.StrictMode>
    <Provider store={store}>
      <QueryClientProvider client={queryClient}>
        <BrowserRouter>
          <ApiConfigInitializer>
            <App />
          </ApiConfigInitializer>
        </BrowserRouter>
      </QueryClientProvider>
    </Provider>
  </React.StrictMode>
);
