import { io, Socket } from 'socket.io-client';
import { store } from '../store';
import { connectionEstablished, connectionLost } from '../features/websocket/websocketSlice';
import { addNotification } from '../features/notifications/notificationsSlice';
import { addMessage, setOnlineUsers, addOnlineUser, removeOnlineUser } from '../features/chat/chatSlice';
import ports from '../config/ports';

const isDevelopment = process.env.NODE_ENV === 'development';

// URL  
const urls = ports.getUrls();

class WebSocketService {
  private socket: Socket | null = null;
  private reconnectAttempts = 0;
  private maxReconnectAttempts = 5;
  private reconnectTimeout = 1000; // 1

  connect() {
    const socketURL = urls.WS;
    console.log('  :', socketURL);
    
    if (this.socket?.connected) {
      console.log('   .');
      return;
    }

    //    
    if (this.socket) {
      this.socket.removeAllListeners();
      this.socket.close();
      this.socket = null;
    }
    
    this.socket = io(socketURL, {
      transports: ['websocket'],
      reconnection: true,
      reconnectionAttempts: this.maxReconnectAttempts,
      reconnectionDelay: this.reconnectTimeout,
      timeout: 10000,
      autoConnect: true,
      forceNew: true,
      auth: {
        token: localStorage.getItem('token')
      }
    });

    this.socket.on('connect', () => {
      console.log('  :', this.socket?.id);
      this.reconnectAttempts = 0;
      store.dispatch(connectionEstablished());
    });

    this.socket.on('disconnect', (reason) => {
      console.log('  :', reason);
      if (reason === 'io server disconnect') {
        //      
        this.connect();
      }
    });

    this.socket.on('error', (error) => {
      console.error(' :', error);
    });

    this.socket.on('connect_error', (error) => {
      console.error('  :', error);
      if (error.message === 'Invalid token') {
        console.log('  .  .');
        //    
        localStorage.removeItem('token');
        //      
      }
    });

    this.socket.on('reconnect_attempt', (attemptNumber) => {
      if (isDevelopment) {
        console.log(`   ${attemptNumber}/${this.maxReconnectAttempts}`);
      }
    });

    this.socket.on('reconnect_failed', () => {
      if (isDevelopment) {
        console.error('  ');
      }
    });

    //   
    this.socket.on('notification', (data) => {
      store.dispatch(addNotification(data));
    });

    this.socket.on('message', (data) => {
      store.dispatch(addMessage(data));
    });

    this.socket.on('onlineUsers', (data) => {
      store.dispatch(setOnlineUsers(data));
    });

    this.socket.on('userJoined', (data) => {
      store.dispatch(addOnlineUser(data));
    });

    this.socket.on('userLeft', (data) => {
      store.dispatch(removeOnlineUser(data));
    });
  }

  private handleReconnect() {
    if (this.reconnectAttempts < this.maxReconnectAttempts) {
      this.reconnectAttempts++;
      setTimeout(() => {
        if (isDevelopment) {
          console.log(`  ${this.reconnectAttempts}/${this.maxReconnectAttempts}`);
        }
        this.connect();
      }, this.reconnectTimeout * this.reconnectAttempts);
    } else {
      if (isDevelopment) {
        console.error('    ');
      }
    }
  }

  disconnect() {
    if (this.socket) {
      this.socket.disconnect();
      this.socket = null;
      console.log('  ');
    }
  }

  on(event: string, callback: (data: any) => void) {
    this.socket?.on(event, callback);
  }

  emit(event: string, data: any) {
    this.socket?.emit(event, data);
  }
}

export const websocketService = new WebSocketService(); 